import React, { Component } from 'react';

function TabPane(props) {
  return (
    <div id={props.id} className="filter-bar__pane">
      {props.content}
    </div>
  );
}

function CheckboxSet(props) {
  return <fieldset className="filter-bar__fieldset">{props.children}</fieldset>;
}

function FilterTab(props) {
  return (
    <div className={`filter-bar__tab ${props.isActive ? 'is-active' : ''}`}>
      <a href={`#${props.id}`} onClick={props.clickHandler}>
        {props.name}
        <span className="filter-bar__tab--full">({props.id})</span>
      </a>
      {props.children}
    </div>
  );
}

function CheckboxFilter(props) {
  return (
    <label htmlFor={props.id} className="filter-bar__label">
      <input
        type="checkbox"
        id={props.id}
        value={`${props.min}, ${props.max}`}
        className="filter-bar__checkbox"
        onChange={props.updateFilter}
        checked={props.filterIsApplied}
      />
      {props.label}
    </label>
  );
}

function RangeSlider(props) {
  return (
    <div className="range-slider">
      <input
        type="range"
        className="range-slider__min"
        name="range-slider__min"
        min="0"
        max="100"
        step="10"
        value={props.min}
        onChange={props.minChangeHandler}
      />
      <div
        className="range-slider__output"
        style={{ width: 2 * (props.max - props.min), left: `${props.min}%` }}
      />
      <input
        type="range"
        className="range-slider__max"
        name="range-slider__max"
        min="0"
        max="100"
        step="10"
        value={props.max}
        onChange={props.maxChangeHandler}
      />
    </div>
  );
}

const filters = [
  {
    name: 'Strength',
    id: 'abv',
    inputType: 'checkbox',
    values: {
      mild: { strength: 'mild', min: 0, max: 3.9, rangeStr: '< 4%' },
      medium: { strength: 'medium', min: 4, max: 7.5, rangeStr: '4.0 - 7.5%' },
      high: { strength: 'high', min: 7.6, max: null, rangeStr: '> 7.5%' }
    }
  },
  {
    name: 'Color',
    id: 'ebc',
    inputType: 'checkbox',
    values: {
      straw: { color: 'straw', min: 4, max: 7, rangeStr: '4 - 7' },
      gold: { color: 'gold', min: 8, max: 17, rangeStr: '8 - 17' },
      amber: { color: 'amber', min: 18, max: 38, rangeStr: '18 - 38' },
      brown: { color: 'brown', min: 39, max: 78, rangeStr: '39 - 78' },
      black: { color: 'black', min: 79, max: null, rangeStr: '79+' }
    }
  },
  {
    name: 'Bitterness',
    id: 'ibu',
    inputType: 'range',
    scale: {
      min: 0,
      max: 100,
      step: 10
    }
  }
];

export default class FilterBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTab: null,
      abvFilter: ['mild', 'medium', 'high'],
      ebcFilter: ['straw', 'gold', 'amber', 'brown', 'black'],
      ibuFilter: {
        min: 0,
        max: 100
      }
    };
    this.clickHandler = this.clickHandler.bind(this);
    this.updateFilter = this.updateFilter.bind(this);
    this.minChangeHandler = this.minChangeHandler.bind(this);
    this.maxChangeHandler = this.maxChangeHandler.bind(this);
  }

  clickHandler(id, e) {
    e.preventDefault();
    this.setState((prevState) => ({
      currentTab: prevState.currentTab !== id ? id : null
    }));
  }

  updateFilter(id, filter, e) {
    const isChecked = e.target.checked === true;
    const filterProp = `${id}Filter`;
    this.setState((prevState) => ({
      [filterProp]: isChecked
        ? prevState[filterProp].concat(filter)
        : prevState[filterProp].filter((item) => item !== filter)
    }));
  }

  minChangeHandler(e) {
    const newMin = +e.target.value;
    this.setState((prevState) => ({
      ibuFilter: {
        ...prevState.ibuFilter,
        min: newMin < prevState.ibuFilter.max ? newMin : prevState.ibuFilter.min
      }
    }));
  }

  maxChangeHandler(e) {
    const newMax = +e.target.value;
    this.setState((prevState) => ({
      ibuFilter: {
        ...prevState.ibuFilter,
        max: newMax > prevState.ibuFilter.min ? newMax : prevState.ibuFilter.max
      }
    }));
  }

  render() {
    console.log(this.state.currentFilter);
    const checkboxOptions = (obj) => {
      const checkboxes = Object.keys(obj.values).map((key) => (
        <CheckboxFilter
          key={key}
          id={key}
          label={`${key} (${obj.values[key].rangeStr})`}
          min={obj.values[key].min}
          max={obj.values[key].max}
          filterIsApplied={this.state[`${obj.id}Filter`].indexOf(key) !== -1}
          updateFilter={(e) => this.updateFilter(obj.id, key, e)}
        />
      ));
      return checkboxes;
    };
    const filterTabs = filters.map((filter) => (
      <FilterTab
        clickHandler={(e) => this.clickHandler(filter.id, e)}
        key={filter.id}
        id={filter.id}
        name={filter.name}
        isActive={this.state.currentTab === filter.id}
      >
        {this.state.currentTab === filter.id && (
          <TabPane
            id={filter.id}
            content={
              filter.inputType === 'checkbox' ? (
                <CheckboxSet>{checkboxOptions(filter)}</CheckboxSet>
              ) : (
                <div className="range-slider__wrapper">
                  <RangeSlider
                    min={this.state.ibuFilter.min}
                    max={this.state.ibuFilter.max}
                    minChangeHandler={(e) => this.minChangeHandler(e)}
                    maxChangeHandler={(e) => this.maxChangeHandler(e)}
                  />
                </div>
              )
            }
          />
        )}
      </FilterTab>
    ));

    return (
      <div className="filter-bar">
        <div className="filter-bar__title">Filter by:</div>
        <div className="filter-bar__tabs">{filterTabs}</div>
      </div>
    );
  }
}
